<template>
    <div class="catalog">
        <ul>
            <li class="module" v-for="(todo,index) in calatogList">
              <h4 :id="todo.id" :pagestart="todo.StartPage" :pageend="todo.EndPage" @click="clickAct" :class="['id_'+todo.id]">{{ todo.title }}</h4>
              <ul>
                <li :class="['unitclass','id_'+sub.id]" :id="sub.id" :pagestart="sub.StartPage" :pageend="sub.EndPage" v-for="sub in todo.children" @click="clickAct">{{ sub.title }}</li>
              </ul>
            </li>
        </ul>
    </div>
</template>

<script>

export default {
  name: 'Catalog',
  data () {
    return {
      //目录数据
      calatogList: [
        /*{
          "id": 294266,
          "title": "第一单元",
          "children": [
            {
              "id": 304419,
              "title": "第一组",
              "isFolder": false,
              "sord": 0,
              "StartPage": 1,
              "EndPage": 1
            },
            {
              "id": 294267,
              "title": "1 观潮",
              "isFolder": false,
              "sord": 1,
              "StartPage": 2,
              "EndPage": 5
            }
          ],
          "isFolder": true,
          "sord": 52,
          "StartPage": 21,
          "EndPage": 39
        },
        {
          "id": 294292,
          "title": "第五单元",
          "children": [
            {
              "id": 294296,
              "title": "语文园地五",
              "isFolder": false,
              "sord": 6,
              "StartPage": 96,
              "EndPage": 98
            }
          ],
          "isFolder": true,
          "sord": 52,
          "StartPage": 81,
          "EndPage": 98
        },
        {
          "id": 294292,
          "title": "第五单元",
          "children": [],
          "isFolder": true,
          "sord": 52,
          "StartPage": 81,
          "EndPage": 98
        }*/
      ],
      //单元起始页码集合
      pageArr: [

      ],
      ss: [
        {
          "id": 294266,
          "title": "第一单元",
          "children": [
            {
              "id": 304419,
              "title": "第一组",
              "isFolder": false,
              "sord": 0,
              "StartPage": 1,
              "EndPage": 1
            },
            {
              "id": 294267,
              "title": "1 观潮",
              "isFolder": false,
              "sord": 1,
              "StartPage": 2,
              "EndPage": 5
            }
          ],
          "isFolder": true,
          "sord": 52,
          "StartPage": 21,
          "EndPage": 39
        },
        {
          "id": 294292,
          "title": "第五单元",
          "children": [
            {
              "id": 294296,
              "title": "语文园地五",
              "isFolder": false,
              "sord": 6,
              "StartPage": 96,
              "EndPage": 98
            }
          ],
          "isFolder": true,
          "sord": 52,
          "StartPage": 81,
          "EndPage": 98
        },
        {
          "id": 294292,
          "title": "第五单元",
          "children": [],
          "isFolder": true,
          "sord": 52,
          "StartPage": 81,
          "EndPage": 98
        }
      ]
    }
  },
  updated() {
//    this.firstLight()
    this.clickOther()
  },
  methods: {
    //页码切换
    currPageChange(pageNum) {
      //console.log("catalog log page===="+pageNum)
      (pageNum < 1) && (pageNum = 1)
      let pageN = pageNum
      let _thisId
      this.pageArr.forEach((item,index) =>{
        let s = parseInt((item.str).split('-')[0])
        let e = parseInt((item.str).split('-')[1])
        if(pageN >= s && pageN <= e){
          _thisId = item.id
          //console.log('id=='+_thisId)
        }
      })
      //console.log('thisId==='+_thisId)
      //移除所有高亮
      this.removelight()
      //高亮对应页码所在目录
      let dom = this.$el.querySelector('.id_'+_thisId)
      dom?this.$emit('setTitle',dom.innerHTML):this.$emit('setTitle','')
      if(dom){
        dom.classList.add('highlight')
        return dom
      }else {
        return ''
      }

    },

    //目录点击事件
    clickAct(e) {
      let _this = e.currentTarget
      let pagestart = _this.getAttribute('pagestart')
      let pageend = _this.getAttribute('pageend')
      //console.log('pagestart:'+pagestart+'pageend:'+pageend)
      console.log(22222, pagestart)
      this.$emit('goto',pagestart)
      //点击选中目录的状态变化
      //移除所有选中
      this.removelight()
      //根据点击目录的层级高亮对应结构
      if(_this.tagName.toLocaleLowerCase() === 'h4'){   //点击一级目录
        if(_this.nextElementSibling.children.length>0)    //点击单元，高亮自己及单元下的第一个子集（有子集的情况），否则只高亮自己
          _this.nextElementSibling.children[0].classList.add('highlight')
        else
          _this.classList.add('highlight')
      }else{  //点击二级目录
        //console.log('this is li')
        _this.classList.add('highlight')
      }
      //通过调用父组件方法，改变同级子组件的状态
      //this.$emit('setTitle',this.getCurText())
    },

    //移除所有选中高亮
    removelight() {
      let allH4 = Array.prototype.slice.call(this.$el.querySelectorAll('.module h4'))
      let allLi = Array.prototype.slice.call(this.$el.querySelectorAll('.module ul li'))
      allH4.forEach((item) => {
        item.classList.remove('highlight')
      })
      allLi.forEach((item) => {
        item.classList.remove('highlight')
      })
    },

    highlight() {

    },

    //初始化目录
    initList(data, UnitID) {
      let me = this
      let _UnitId
      //data = this.ss
      //this.calatogList = data
      this.$set(this, 'calatogList', data)
      this.regroupData(this.calatogList)

      /*处理高亮*/
      if(UnitID == undefined || UnitID == ''){
        if(data[0] && data[0]['children']){
          UnitID = data[0]['children'][0]['id']
        }else{
          UnitID = data[0]['id']
        }
        _UnitId = UnitID
      }

      return new Promise((resolve,reject)=>{
        this.$nextTick(() => {
          let dom  = this.$el.querySelector('.id_'+UnitID)
          dom && dom.classList.add('highlight')
          //_UnitId && me.$emit('goto',dom.getAttribute('pagestart'))
          let d = {
            UnitID:_UnitId,
            PageNum:dom.getAttribute('pagestart')
          }
          resolve(d)
        })
      })
    },
    //组装单元起始页码集合
    regroupData(data) {
      let _data = data
      let arr = []
      let str
      let id
      _data.forEach((item,index) =>{
        str = item.StartPage+'-'+item.EndPage
        id = item.id
        arr.push({id,str})
        //判断一级目录是否有子集
        if(item.children){
          item.children.forEach((item,index) =>{
            str = item.StartPage+'-'+item.EndPage
            id = item.id
            arr.push({id,str})
          })
        }
      })

//      this.pageArr = arr
      this.$set(this,'pageArr',arr)
      //console.log(JSON.stringify(this.pageArr))
    },

    //点击catalog和menuA之外的区域，隐藏catalog
    clickOther() {
      document.body.addEventListener('click',function(ev){
        let _target = ev.target
        if(_target.getAttribute('class') !== 'catalog reset' && _target.getAttribute('class') !== 'menuA')
          document.querySelector('.catalog').classList.remove('reset')
        else
          return false
      })
    },

    //高亮第一条信息
    firstLight() {
      this.$el.querySelector('li.module:first-child h4').click()
    },

    //获取当前选中的目录title
    getCurText() {
      let titleText
      if(this.$el.querySelector('li.module .highlight'))
        titleText = this.$el.querySelector('li.module .highlight').innerHTML
      return titleText
    }
  }
}
</script>

<style scoped>
    .catalog{
        width:300px;
        height:100%;
        overflow-x:hidden;
        overflow-y:auto;
        position:fixed;
        top:0;
        left:-300px;
        transition:left 1s;
        -moz-transition:left 1s; /* Firefox 4 */
        -webkit-transition:left 1s; /* Safari and Chrome */
        -o-transition:left 1s; /* Opera */
        z-index:101;
        background-color:rgba(87,112,167,0.85);
    }
    .catalog.reset{
        left:0;
        transition:left 1s;
        -moz-transition:left 1s; /* Firefox 4 */
        -webkit-transition:left 1s; /* Safari and Chrome */
        -o-transition:left 1s; /* Opera */
    }
    .catalog ul{
        padding:0;
        margin:0;
        text-align:left;
    }
    .catalog ul li{
        color:#ffffff;
        min-height:40px;
        line-height:40px;
        border-top:1px #7991af solid;
        cursor:pointer;
        text-overflow:ellipsis;
        white-space:nowrap;
        overflow:hidden;
        font-size:16.8px;
        position:relative;
    }
    .catalog ul li.module h4{
        padding:0 10px 0 20px;
        background-color:rgba(59,89,152,0.85);
    }
    .catalog ul li.module h4.highlight{
        color:#FFA04A;
    }
    .catalog ul li.unitclass{
        padding:0 10px 0 35px;
    }
    .catalog ul li.module ul li.highlight{
        color:#FFA04A;
    }
</style>



