<template>
  <div id="app">
    <ebook ref="swipe" @change="changeSwipe"/>
    <ToolBar ref="barRef"/>
    <Catalog ref="catalogRef" @goto="goto" @setTitle="transmitTitle"/>
    <BtnGroup @prev="prev" @next="next"/>
    <TeachMap ref="tMapRef" @change="initTeachMap" @goto="goto" @open="open"/>
    <Popup ref="popupRef"/>
  </div>
</template>

<script>
  import Vue from 'vue'
  import uuid_v1 from 'uuid/v1'
  import ebook from 'src/components/ebook/index.vue'
  import Popup from 'src/components/popup/index.vue'
  import Catalog from 'src/components/Catalog.vue'
  import ToolBar from 'src/components/ToolBar'
  import BtnGroup from 'src/components/BtnGroup'
  import TeachMap from 'src/components/TeachMap'
  import Utils from 'src/lib/utils'
  import WebJsBridge from 'src/lib/transit'
  import Water from 'src/codeBlocks/water'

  export default {
    name: 'PreLesson',
    data() {
      return {
        initFlag: false,
        debug: true,
        bookList: [
          {pageImg: './assets/images/1.jpg', pageId: -2},
          {
            pageImg: './assets/images/1.jpg',
            pageId: 3,
            buttons: {
              button: [
                {
                  "eventtype": 5,
                  "subjectNum": 1,
                  "itemNum": 0,
                  "soundsrc": "http://183.47.42.218:1777/Course/TextBook/GD_6A/page014/sound/p014003.mp3",
                  "id": 1,
                  "width": 100,
                  "height": 25,
                  "x": 85,
                  "y": 40
                }
              ]
            }
          },
         ],
        btnArr: [
          [
            {
              "id": "p_76",
              "icoType": "5",
              "randId": "1505813969790",
              "isread": true,
              "sourceUrl": "c6837bb1-ea95-4546-94b6-cb72bdd6f694",
              "X": 100,
              "Y": 60,
              "title": "测试1",
              "comeFrom": "ModResource"
            },
            {
              "id": "p_77",
              "icoType": "4",
              "randId": "1505813969790",
              "isread": true,
              "sourceUrl": "c6837bb1-ea95-4546-94b6-cb72bdd6f694",
              "X": 200,
              "Y": 60,
              "title": "测试2",
              "comeFrom": "ModResource"
            }
          ],
          [
            {
              "id": "p_7",
              "icoType": "6",
              "randId": "1505813969790",
              "isread": true,
              "sourceUrl": "c6837bb1-ea95-4546-94b6-cb72bdd6f694",
              "X": 100,
              "Y": 60,
              "title": "测试3",
              "comeFrom": "ModResource"
            }
          ]
        ],
        currentPage: 1,
        UnitID: '',
        UnitName: '2. Ask a partner the questions above.',
        PageInitID: '',
        CreateTime: '',
        PageNum: ''
      }
    },
    components: {
      ebook,
      Catalog,
      ToolBar,
      BtnGroup,
      TeachMap,
      Popup
    },
    created () {
      this.teachPage = this.throttle(name=>{
        this.postTeachPage(name)
      }, 5000)
    },
    mounted() {
      // el 被新创建的 vm.$el 替换，并挂载到实例上去之后调用该钩子。

      // js桥
      let w = new WebJsBridge({
        hideToolbar: this.$refs.popupRef.hideToolbar,
        showToolbar: this.$refs.popupRef.showToolbar,
        getConfig: this.$refs.popupRef.getConfig
      })

      window.getLoginSetting = ()=>{
        let me = this
        // 初始化电子书
        this.initData()
          .then(res => {
            this.bookList = res.pageSource
            let path = !res.DBJsonPath?
              `data/${Utils.getQuery('UserID')}/${Utils.getQuery('BookID')}/${Utils.getQuery('BookName')}/`
              :res.DBJsonPath
            this.$refs.swipe.initData(this.bookList, path)
            return ''
          })
          .then(res => {
            return this.getTeachPage()
          })
          .then(res => {
            if(res) { //不为空
              me.UnitID = res['UnitID']
              me.UnitName = res['UnitName']
              me.CreateTime = res['CreateTime']
              me.PageNum = res['PageNum']
            }
            return ''
          })
          .then(res => {
            return this.initCatalog()
          })
          .then(res => {
           return this.$refs.catalogRef.initList(res, this.UnitID)
          })
          .then(res => {
            this.goto(me.PageNum || res.PageNum)
          })
          /*.then(res => {
            this.UnitID = res
            return this.initTeachMap(this.UnitID)
          })
          .then(res => {
            this.$refs.tMapRef.initMap(res, this.UnitID)
          })*/
          .catch(err=> {

          })
      }
      if(this.osFlag() !== 'android') {
        window.getLoginSetting()
      }
    },
    methods: {
      osFlag (){
        let userAgent = navigator.userAgent
        return userAgent.match(/iphone|ipad|mac/i) ? 'ios' : userAgent.match(/Android/i) ? 'android' : ''
      },
      log (...args) {
        if(this.debug) {
          console.log(...args)
        }
      },
      open (pageId, fileId) {
        this.$refs.popupRef.open(pageId, fileId)
      },
      /*电子书接口*/
      // 电子书初始化
      initData() {
        return new Promise((resolve, reject) => {
          this.$request({
            type: 'GET',
            url: '/api/getDbJsById',
            data: {
              UserID: Utils.getQuery('UserID'),
              BookID: Utils.getQuery('BookID')
            },
            success: (res)=>{
              if(typeof res.Data === 'string') {
                let jsonPath = ''
                if(Vue.onlineEnv || this.osFlag() === 'android') {
                  let tData = res.Data
                  let len = tData.length
                  jsonPath = '/' + tData.substring(tData.indexOf("DBJsonPath='")+12, tData.indexOf("';var dbJson")) + '/'
                  res.Data = tData.substring(tData.indexOf("dbJson='")+8, len - 2)
                }else {
                  jsonPath = JSON.parse(res.Data)
                }
                res.Data = JSON.parse(res.Data)
                Vue.onlineEnv && (res.Data['DBJsonPath'] = jsonPath)
              }
              if(res.Success && res.Data && res.Data.pageSource) {
                resolve(res.Data)
              }else {
                reject(res)
              }
            },
            error: (err)=>{
              reject(err)
            }
          })
        })
      },

      // 电子书页码跳转(已优化为pageId)
      goto(index) {
//        return
//        index = this.$el.querySelector('.pageNum').value
        this.$refs.swipe.goto(index);
      },

      // 上一屏
      prev() {
        this.$refs.swipe.prev();
      },

      // 下一屏
      next() {
        this.$refs.swipe.next();
      },

      throttle (cb, delay, wait) {
        let timer,start
        return function () {
          let now = new Date().getTime()
          let context = this
          let args = arguments
          if(!start) {
            start = now
          }
          if(wait && (now - start > wait)) {
            timer && clearTimeout(timer)
            start = now
            cb.apply(context, args)
          }else {
            timer && clearTimeout(timer)
            timer = setTimeout(function () {
              cb.apply(context, args)
            }, delay)
          }
        }
      },

      postTeachPage (UnitName) {
        !this.PageInitID && (this.PageInitID = uuid_v1())
        !this.CreateTime && (this.CreateTime = Utils.formatTime(new Date().getTime()))
//        !this.UnitID && (this.UnitID = '308819')
        return new Promise((resolve, reject)=>{
          this.$request({
            type: 'GET',
            url: '/api/postTeachPage',
            data: {
              pagedata: {
                PageInitID: this.PageInitID,
                UserID: Utils.getQuery('UserID'),
                GradeID: Utils.getQuery('GradeID'),
                ClassID: Utils.getQuery('ClassID'),
                BookID: Utils.getQuery('BookID'),
                SubjectID: Utils.getQuery('SubjectID'),
                UnitID: this.UnitID,
                PageNum: this.currentPage,
                CreateTime: this.CreateTime,
                AspxName: 'Teaching',
                EditionID: Utils.getQuery('EditionID'),
                BookType: Utils.getQuery('BookType'),
                Stage: null,
                UnitName: UnitName
              },
              UserID: Utils.getQuery('UserID'),
              BookID: Utils.getQuery('BookID'),
              ClassID: Utils.getQuery('ClassID'),
              AspxName: 'Teaching'
            },
            success: (res)=>{
              if(res.Success) {
                if(typeof res.Data === 'string'){
                  res.Data = JSON.parse(res.Data)
                }
                resolve(res.Data)
              }else {
                reject(res)
              }
            },
            error: (err)=>{
              reject(err)
            }
          })
        })
      },

      getWater (pageNumStr) {
        new Promise((resolve, reject) => {
          this.$request({
            type: 'GET',
            url: '/api/selBookPageData',
            data: {
              UserID: Utils.getQuery('UserID'),
              BookID: Utils.getQuery('BookID'),
              Pages: pageNumStr //"2,3"
            },
            success: (res)=>{
              (typeof res.Data === 'string') && (res.Data = JSON.parse(res.Data))
              if(res.Success && res.Data && res.Data.length) {
                resolve(res.Data)
              }else {
                reject(res)
              }
            },
            error: (err)=>{
              reject(err)
            }
          })
        }).then(res=>{
          let btnArr = []
          let pageArr = []
          res.forEach(item=>{
            let tmp = JSON.parse(item)
            btnArr.push(tmp.btns)
            pageArr.push(tmp.pageNum)
          })
          this.btnArr = btnArr
          this.log('water data:', btnArr)
          this.water.add(pageArr, this.btnArr)
        }).catch(err=>{})
      },

      // 切换屏(已优化为左侧pageId)
      changeSwipe(newIndex, oldIndex, lazyArr, towards) {
        console.log(`swipe from ${oldIndex} to ${newIndex}`);
        if(!this.initFlag) {
          this.water = new Water({
            open: this.$refs.popupRef.open,
            pxRate: this.$refs.swipe.pxRate,
            currentPage: this.currentPage
          })
        }
        if(newIndex + 1 > 0) {
          this.getWater(newIndex !== 0?`${newIndex},${newIndex + 1}`:`${newIndex + 1}`)
        }
        if(towards === 'prev' || towards === 'next' || towards ==='goto') {
          this.currentPage = newIndex;
          let dom = this.$refs.catalogRef.currPageChange(newIndex)
          this.teachPage(dom.innerText)
          if(!this.initFlag || this.UnitID !== dom.id){ //按单元请求教学地图
            this.initFlag = true
            this.initTeachMap(dom.id).then(res=>{
              console.log('教学地图数据'+res)
              this.$refs.tMapRef.initMap(res, dom.id)
              this.UnitID = dom.id
            },err=>{})
          }

          //this.$refs.tMapRef.currPageChange(newIndex, dom)
        }
      },

      /*电子书接口end*/

      /*获取授课页码*/
      getTeachPage () {
        return new Promise((resolve, reject)=>{
          this.$request({
            type: 'GET',
            url: '/api/getTeachPage',
            data: {
              UserID: Utils.getQuery('UserID'),
              BookID: Utils.getQuery('BookID'),
              ClassID: Utils.getQuery('ClassID'),
              AspxName: 'Teaching'
            },
            success: (res)=>{
              if(res.Success) {
                if(typeof res.Data === 'string' && res.Data !== ''){
                  res.Data = JSON.parse(res.Data)
                }
                resolve(res.Data)
              }else {
                reject(res)
              }
            },
            error: (err)=>{
              reject(err)
            }
          })
        })
      },

      /*请求电子书目录*/
      initCatalog(id) {
        return new Promise((resolve, reject)=>{
          this.$request({
            type: 'GET',
            url: '/api/getCatalogByBookId',
            data: {
              UserID: Utils.getQuery('UserID'),
              BookID: Utils.getQuery('BookID')
            },
            success: (res)=>{
              if(res.Success) {
                if(typeof res.Data === 'string'){
                  res.Data = JSON.parse(res.Data)
                }
                resolve(res.Data)
              }else {
                reject(res)
              }
            },
            error: (err)=>{
              reject(err)
            }
          })
        })
      },

      /*请求教学地图*/
      initTeachMap(Id) {
        return new Promise((resolve, reject)=>{
          this.$request({
            type: 'GET',
            url: '/api/getCurTeachMap',
            data: {
              UserID: Utils.getQuery('UserID'),
              BookID: Utils.getQuery('BookID'),
              UnitID: Id
            },
            success: (res)=>{
              if(res.Success) {
                if(typeof res.Data === 'string'){
                  res.Data !== '' && (res.Data = JSON.parse(res.Data))
                }
                resolve(res.Data)
              }else {
                reject(res)
              }
            },
            error: (err)=>{
              reject(err)
            }
          })
        })
      },
      //传递title
      transmitTitle(data) {
        this.$refs.barRef.initTitle(data);
      }
    }
  }
</script>

<style></style>



