import Utils from 'src/lib/utils'

export default {
  optIdObj: {
    resOpt: 1302,  // 资源
    innerResOpt: { // 内置资源
      val: 1303,
      child: {
        spotRead: 130301,
        listen_write: 130302,
        allRead: 130303
      }
    }
  },

  getVal (p, k) {
    let arr = []
    if(typeof k === 'string') {
      return p[k]
    }else {
      k.forEach(item=>{
        arr.length === 0
          ? arr.push(p[item])
          : arr.push(arr.pop()[item])
      })
      return arr.pop()
    }
  },

  userOptLog (param) {
    Utils.request({
      type: 'GET',
      url: '/api/userOperLog',
      data: {
        UserID: Utils.getQuery('UserID'),
        userType: 12,
        operId: this.getVal(this.optIdObj, param.operId),
        content: JSON.stringify(param.data)
      },
      success: (res)=>{

      },
      error: (err)=>{

      }
    })

  }
}



