import axios from 'axios'
import Vue from 'vue'
import sClassJSBridge from './jsBridge'
import Config from '../../config/serverConfig'

export default {
  request(opt) {
    // let async = typeof opt.async === 'undefined'? true:opt.async;
    // let contentType = opt.contentType || 'application/x-www-form-urlencoded';
    // let dataType = opt.dataType || 'text';
    let nop = function () {};
    let type = opt.type;
    let url = opt.url;
    let data = opt.data || null;
    let success = opt.success || nop;
    let error = opt.error || nop;
    let hostName = Config.apiServer

    if(!Vue.onlineEnv) {
      let apiName = url.split('/').pop();
      sClassJSBridge.call(apiName, data, success);
    }else {
      let obj = {
        method: type.toLowerCase(),
        url: hostName + url
        // url: url
      }
      let key = obj.method === 'get'? 'params':'data'
      obj[key] = data

      axios(obj)
      .then((res)=>{
        // console.log('接口：', url, JSON.parse(res.data));
        success(res.data)
      }, error)
    }
  },
  getQuery (name) {
    let reg = new RegExp("(?:^|&)"+ name +"=([^&]*)(?:&|$)");
    let res = window.location.search.substr(1).match(reg);
    if(res !== null){
      return  decodeURIComponent(res[1]);
    }
    return null;
  },
  formatTime (stamp) {
    let D = new Date(stamp)
    let preArr = []
    let afterArr = []

    preArr.push(D.getFullYear())
    preArr.push((D.getMonth()+1).toString().padStart(2, '0'))
    preArr.push(D.getDate().toString().padStart(2, '0'))

    afterArr.push(D.getHours().toString().padStart(2, '0'))
    afterArr.push(D.getMinutes().toString().padStart(2, '0'))
    afterArr.push(D.getSeconds().toString().padStart(2, '0'))
    return `${preArr.join('-')}T${afterArr.join(':')}`
  }
}



