import Vue from 'vue'
import Router from 'vue-router'
import Pages from 'src/pages'

Vue.use(Router)

const router = new Router({
    component: Pages.Root,
    routes: [
        {
            path: '/Lesson',
            component: Pages.Lesson
        },
        {
            path: '/Bookcase',
            component: Pages.Bookcase
        },
        {
          path: '/Login',
          component: Pages.Login
        }
    ],
    mode: 'hash'
    // mode: 'history'
})

export default router



