<template>
    <div class="wrap">
        <router-view></router-view>
    </div>
</template>
<style>
  * {
    padding: 0;
    margin: 0;
    text-decoration: none;
  }

  html, body,.wrap {
    width: 100%;
    height: 100%;
  }
  body {
    background-color: #fff;
    position: relative;
    font-size: 16px;
  }
  #app {
    font-family: 'Avenir', Helvetica, Arial, sans-serif;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
    text-align: center;
    color: #2c3e50;
    height: 100%;
  }
  /*重置滚动条样式*/
  ::-webkit-scrollbar{width:8px;} /*滚动条整体部分，其中的属性有width,height,background,border（就和一个块级元素一样）等。*/
  ::-webkit-scrollbar-button{display:none;} /*滚动条两端的按钮。可以用display:none让其不显示，也可以添加背景图片，颜色改变显示效果。*/
  ::-webkit-scrollbar-track{display:none} /*外层轨道。可以用display:none让其不显示，也可以添加背景图片，颜色改变显示效果。*/
  ::-webkit-scrollbar-track-piece{display:none;}  /*内层轨道，滚动条中间部分（除去）。*/
  ::-webkit-scrollbar-thumb{background:#cecfd3;border-radius:6px;opacity: 0.85;}  /*滚动条里面可以拖动的那部分*/
  ::-webkit-scrollbar-corner{}  /* 边角*/
  ::-webkit-resizer{}

  button{
    background:none;
    border:0;
  }
</style>
<script>
    import 'babel-polyfill'
    import router from 'src/router'
    import store from 'src/vuex'
    import filters from 'src/filters'
    import 'src/lib/jsBridge'

    export default {
        router,
        store,
        filters,
        name: 'root',
        components: {},
        created () {
            this.$router.replace({
                path: 'Lesson'
//                path: 'Bookcase'
            })
        }
    }
</script>



