<template>
  <div>
    <div class="bookList">
      <ul class="bookWrap">
        <li @click="startTeaching" v-for="item in bookList">
          <img :src="item.thumbnail">
          <i></i>
          <span>{{item.name}}</span>
        </li>
      </ul>
      <img src="../assets/images/bookrack.png"/>
    </div>
    <div class="footer">
      <a href="javascript:void();">更多上课教材></a>
    </div>
  </div>
</template>
<style scoped>
  .bookList {
    width: 12.8rem;
    margin: 0 auto;
    position: relative;
  }
  .bookList > img {
    width: 100%;
    float: left;
    margin-top: -0.55rem;
  }
  .bookWrap {
    list-style: none;
    display: flex;
    justify-content: space-around;
    align-items: flex-start;
  }
  .bookList li{
    float: left;
    width: 2rem;
    cursor: pointer;
    position: relative;
  }
  .bookList li img {
    width: 100%;
    border-radius: 3px;
    box-shadow: 4px 0 5px 6px rgba(146, 146, 146, .3);
  }
  .bookList li i{
    position: absolute;
    top: 0.07rem;
    width: 0;
    height: 2.6rem;
    display: inline-block;
    background-color: #999da4;
    box-shadow: 4px 0 5px 6px rgba(146, 146, 146, .3);
  }
  .bookList li span {
    position: absolute;
    bottom: -0.8rem;
    display: inline-block;
    width: 100%;
    color: #666;
    z-index: 10;
  }
  .footer {
    padding-top: .2rem;
    text-align: center;
  }
  .footer a {
    color: #90949c;
  }
</style>
<script>

export default {
  name: 'BookList',
  data () {
    return {
      bookList: [
        {
          name: '牛津深圳版小学英语四年级上册',
          thumbnail: 'http://183.47.42.218:1777/TextBookHandler.ashx?OP=getImg&BookID=171'
        },
        {
          name: '牛津深圳版小学英语二年级下册',
          thumbnail: 'http://183.47.42.218:1777/TextBookHandler.ashx?OP=getImg&BookID=264'
        },
        {
          name: '北师大版小学数学四年级上册',
          thumbnail: 'http://183.47.42.218:1777/TextBookHandler.ashx?OP=getImg&BookID=440'
        }
      ]
    }
  },
  methods: {
    startTeaching () {
      this.$router.push({
        path: 'PreLesson'
      })
    }
  }
}
</script>





