import './water.css'
import optLog from 'src/codeBlocks/optLog'
export default class {
  constructor (param) { //param {pxRate:,open:,getConfig}
    this.pxRate = param.pxRate || 1
    let me = this
    this._q('body').on('click', '.ebook section div.icon', function(e){
      let info = JSON.parse(this.getAttribute('data-info'))
      let type = this.classList.contains('icon10')? 'PPT':'Other'
      let pageId = Number.parseInt(this.parentNode.dataset['page'])
      // 用户操作记录
      /*optLog.userOptLog({
        operId: 'resOpt',
        data: {
          resourceType: info['icoType'],
          resourceId: info['sourceUrl'],
          title: info['title']
        }
      })*/
      param.open(pageId, info.sourceUrl, type)
    })
  }

  _q (dom) {
    let pDom = typeof dom === 'string'? document.querySelector(dom) : dom
    try {
      pDom.on = function (evt, subDom, cb, flag) {
        if(typeof subDom === 'function'){
          if(typeof cb === 'undefined') {
            pDom.addEventListener(evt, subDom)
          }else {
            pDom.addEventListener(evt, subDom, cb)
          }
        }else {
          if(typeof flag === 'undefined') {
            pDom.addEventListener(evt, function (e) {
              let sDom = Array.prototype.slice.call(pDom.querySelectorAll(subDom))
              sDom.forEach(function (item, idx) {
                if(item.contains(e.target)) {
                  cb.bind(item)(e)
                }
              })
            })
          }else {
            pDom.addEventListener(evt, function (e) {
              let sDom =  Array.prototype.slice.call(pDom.querySelectorAll(subDom))
              sDom.forEach(function (item, idx) {
                if(item.contains(e.target)) {
                  cb.bind(item)(e)
                }
              })
            }, flag)
          }
        }
      }
    }catch (e) {
      console.log('bind element error:', dom)
      console.log(e)
    }
    return pDom
  }

  _parseDom (str) {
    let dom = document.createElement("div")
    if(typeof str === "string"){
      dom.innerHTML = str
    }
    return Array.prototype.slice.call(dom.childNodes, 0)
  }

  add(pageArr, dataArr) {
    let pxRate = this.pxRate
    let remBase = Number.parseFloat(document.documentElement.style.fontSize)
    pageArr.forEach((item, idx)=>{
      let pageDom = document.querySelector(`.ebook ul .page${item}`)
      if(!(pageDom && pageDom.classList.contains('icon'))) {
        let dArr = dataArr[idx]
        let fragDom = document.createDocumentFragment()
        let tpl = ''

        dArr.forEach(dataObj =>{
          tpl += `<div class="icon icon${dataObj.icoType}" style="left: ${(dataObj.X*pxRate/remBase).toFixed(3)}rem; top: ${(dataObj.Y*pxRate/remBase).toFixed(3)}rem;" title="${dataObj.title.replace(/'/g, "\'")}" data-info='${JSON.stringify(dataObj)}'></div>`
        })

        this._parseDom(tpl).forEach(item => {
          fragDom.appendChild(item)
        })
        pageDom.appendChild(fragDom)
      }
    })

  }
}



