<template>
  <div class="loginDiv bgImg">
    <div class="loginBox">
      <a class="set" @click="setIpAndPort"></a>
      <ul>
        <li class="user">
          <span><input name="txt_Account" type="text" id="txt_Account" placeholder="账号" v-model="userName" spellcheck="false" autocomplete="off"></span>
          <p class="validationTip" id="account"></p>
        </li>
        <li class="pass">
          <span><input name="txt_Password" type="password" id="txt_Password" v-model="userPwd" placeholder="密码"></span>
          <p id="password" class="validationTip"></p>
        </li>
      </ul>
      <p class="pS1">忘记密码请联系学校管理员！</p>
      <p class="pS2">
        <input type="submit" name="btn_Login" value="登 录" @click="checkUser" id="btn_Login" class="loginBtn">
      </p>
    </div>
    <Tips ref="Tips"></Tips>
    <div class="copyRight">©&nbsp;2017&nbsp;&nbsp;&nbsp;&nbsp;深圳市方直科技股份有限公司</div>
  </div>
</template>

<script>
  import Utils from 'src/lib/utils'
  import Tips from 'src/components/Tips'

  export default {
    name: 'Login',
    components: {
      Tips
    },
    data() {
      return {
        userName: '',
        userPwd: ''
      }
    },
    mounted() {
      window.getLoginSetting = ()=>{console.log('js桥已载入')}
      document.addEventListener('keydown',(event)=>{
        if(event.keyCode === 13)
          this.checkUser()
      })
    },
    methods: {
      //离线验证
      checkUser() {
        //用户名密码不为空
        if(!this.isNull(this.userName) && !this.isNull(this.userPwd)){
          //离线验证
          new Promise((resolve, reject) => {
            this.$request({
              type: 'GET',
              url: '/api/checkUser',
              data: {
                name: this.userName,
                pwd: this.userPwd
              },
              success: (res) => {
                if (res.Success) {
                  resolve(res)
                } else {
                  reject(res)
                }
              },
              error: (err) => {
                reject(err)
              }
            })
          }).then(
            res =>{
            //验证通过
            this.$router.replace({
              path: 'BookCase'
            })
          },err =>{
            this.$refs.Tips.initData('账号或密码有误，请重新输入')
          }),err =>{}
        }else{
          if(this.isNull(this.userName)){
            this.$refs.Tips.initData('账号不能为空')
          }else if(this.isNull(this.userPwd)){
            this.$refs.Tips.initData('密码不能为空')
          }
        }
      },
      //设置ip
      setIpAndPort() {

      },
      //判断是否为空
      isNull(str){
        if(str === "")
          return true;
        let regu = "^[ ]+$";
        let re = new RegExp(regu);
        return re.test(str);
      }
    }
  }
</script>

<style scoped>
  *{
    border:0;
    padding:0;
    margin:0;
    font-family:"微软雅黑";
  }
  ul{
    list-style: none;
  }
  .bgImg{
    width:100%;
    height:100%;
    min-height:500px;
    background:url(../assets/homeBg.jpg) center center no-repeat;
    background-size:cover;
    position:relative;
  }
  .loginBox{
    display:inline-block;
    width:500px;
    background-color:rgba(255,255,255,0.9);
    position:absolute;
    top:50%;
    left:50%;
    -webkit-transform:translate(-50%,-50%);
    transform:translate(-50%,-50%);
    border-radius:10px;
  }
  .loginBox .set{
    position:absolute;
    right:0;
    top:0;
    display:block;
    width:70px;
    height: 50px;
    color: #666666;
    font-size:14px;
    cursor: pointer;
    background: url(../assets/login.png) no-repeat center 23px;
  }
  .loginBox .set:hover{
    color:#677c97;
  }
  .loginBox ul{
    padding:70px 70px 0 70px;
  }
  .loginBox ul li{
    position:relative;
  }
  .loginBox ul li span{
    margin-bottom:30px;
    width:310px;
    background:#f7f7f7 url(../assets/login.png) no-repeat 13px -32px;
    display:inline-block;
    padding-left:50px;
    height:45px;
    border:1px #a7a7a7 solid;
  }
  .loginBox ul li span input{
    height:45px;
    line-height:45px;
    font-size:24px;
    width:310px;
    border:0;
    background-color:#f7f7f7;
    border-radius:0;
    color:#2d3649;
  }
  .loginBox ul li span input:focus,.loginBox ul li span input:-webkit-autofill{
    -webkit-box-shadow:0 0 0 1000px #f7f7f7 inset;
    outline:none;
  }
  .loginBox ul li.pass span{
    background-position:13px -76px;
  }
  .loginBox ul li a{
    font-size:20px;
  }
  .loginBox ul li .validationTip{
    position: absolute;
    bottom: 7px;
    left: 50px;
    font-size:14px;
    color:#666;
  }
  .loginBox p .loginBtn{
    display:inline-block;
    width:360px;
    height:50px;
    line-height:50px;
    text-align:center;
    background-color:#3b5998;
    color:#FFFFFF;
    border-radius:25px;
    font-size:24px;
    border:none;
    outline:none;
    -webkit-appearance:none;
    cursor:pointer;
  }
  .loginBox p.pS1{
    color:#90949c;
    font-size:16px;
    margin:0 70px 50px 0;
    text-align:right;
  }
  .loginBox p.pS2{
    text-align:center;
    margin:20px 0 50px 0;
  }
  .copyRight{
    margin-bottom:20px;
    width:100%;
    text-align:center;
    font-size:16px;
    color:#2d3649;
    position:absolute;
    bottom:0;
  }
</style>



