<template>
    <div>
      <header>
        <div class="left">
          <img class="avatar" src="http://183.47.42.218:8022/KingsunFiles/AvatarFile/6d363b38-d268-46c4-92ab-46de283317e2.jpg?random=0.9944403291962007"/>
          <span class="name">欢迎您,{{teacherName}}老师</span>

          <!--<select>
            <option value="commonClass" @click="Prelesson">公共班级</option>
          </select>-->
        </div>
        <!--下拉班级列表-->
        <div class="classSelect" @click="selectClick">
          <label>{{ this.selectValue }}</label>
          <i></i>
          <div class="classList">
            <i></i>
            <ul>
              <li classid="-1" gradeid="-1" class="on" @click="selectAct">公共班级</li>
              <li classid="1" gradeid="2" @click="selectAct">二年级圆梦班</li>
            </ul>
          </div>
        </div>
        <div class="right">
          <span>
            <p class="week">星期四</p>
            <p class="date">2018-2-08</p>
          </span>
          <p class="time">13:50</p>
          <!--<span class="week">星期三</span>
          <span class="time">{{new Date()}}</span>-->
        </div>
      </header>
      <section class="bookList_wrap">
        <book-list></book-list>
      </section>
      <footer>
        <button>去备课</button>
      </footer>
    </div>
</template>

<script>
    import BookList from 'src/components/BookList.vue'
    export default {
      name: 'Bookcase',
      data() {
        return {
          teacherName: 'reamd',
          classList:[],
          selectValue:'公共班级'
        }
      },
      components: {
        BookList
      },
      mounted() {

      },
      methods: {
        //下拉框点击事件
        selectClick() {
          let _this = event.currentTarget
          if(_this.classList.contains('on')){
            _this.classList.remove('on')
          }else{
            _this.classList.add('on')
          }
        },
        //下拉选中事件
        selectAct() {
          let _this = event.currentTarget
          let _value = _this.innerHTML
          let _li = Array.prototype.slice.call(_this.parentNode.querySelectorAll('li'))
          _li.forEach((item) =>{
            item.classList.remove('on')
          })
          _this.classList.add('on')
          this.selectValue = _value
        },
        //去备课
        Prelesson() {

        }
      }
    }
</script>

<style scoped>
  *{
    font-family:'微软雅黑';
  }
  ul{
    list-style:none;
  }
  header{
    padding:8px 5%;
    height:84px;
    border-bottom:1px #dbdbdb solid;
  }
  header .left{
    height:100%;
    display:inline-block;
    vertical-align:middle;
  }
  header .left img.avatar,header .left span.name{
    display:inline-block;
    vertical-align:middle;
  }
  header .left img.avatar{
    height:calc(100% - 6px);
    border:3px #e6eaf2 solid;
    border-radius:80px;
    vertical-align:middle;
  }
  header .left span.name{
    margin-left:15px;
    color:#555;
    font-weight:bold;
    vertical-align:middle;
    font-size:1.2em;
  }

  .classSelect{
    height:30px;
    width:180px;
    line-height:30px;
    color:#333333;
    text-align:center;
    cursor:pointer;
    position:relative;
    margin-left:100px;
    display:inline-block;
  }
  .classSelect label{
    color:#555;
    font-size:1.125em;
    vertical-align:middle;
    cursor:pointer;
  }
  .classSelect>i{
    display:inline-block;
    background:url(../assets/homeIcon.png) no-repeat 3px 6px;
    width:20px;
    height:20px;
    vertical-align:middle;
  }
  .classSelect.on>i{
    -webkit-transform: rotate(180deg);
    -moz-transform: rotate(180deg);
    -ms-transform: rotate(180deg);
    -o-transform: rotate(180deg);
    transform: rotate(180deg);
  }
  .classSelect .classList{
    position:absolute;
    left:0;
    border-top-width:10px;
    width:100%;
    display:none;
    z-index:9;
  }
  .classSelect.on .classList{
    display:block
  }
  .classSelect .classList i{
    display:block;
    width:0;
    height:0;
    margin:0 auto;
    border-width:6px 10px;
    border-style:solid;
    border-color:transparent transparent #3b5998 transparent;
    opacity:0.6;
  }
  .classSelect ul{
    background-color:rgba(59,89,152,0.6);
    -webkit-border-radius:8px;
    -moz-border-radius:8px;
    border-radius:8px;
  }
  .classSelect ul li{
    color:#ffffff;
    text-align:center;
    font-size:16px;
    height:40px;
    line-height:40px;
    cursor:pointer;
  }
  .classSelect ul li.on,.fixedLeft ul li.on:hover{
    background-color:#4cafff;
    color:#fff;
  }
  .classSelect ul li:first-child{
    border-top:0;
    -webkit-border-top-left-radius:8px;
    -webkit-border-top-right-radius:8px;
    -moz-border-radius-topleft:8px;
    -moz-border-radius-topright:8px;
    border-top-left-radius:8px;
    border-top-right-radius:8px;
  }
  .classSelect ul li:last-child{
    border-bottom:0;
    -webkit-border-bottom-left-radius:8px;
    -webkit-border-bottom-right-radius:8px;
    -moz-border-radius-bottomleft:8px;
    -moz-border-radius-bottomright:8px;
    border-bottom-left-radius:8px;
    border-bottom-right-radius:8px;
  }
  .classSelect ul li+li{
    border-top:1px #92a3c5 solid;
  }

  header .right{
    float:right;
    display:inline-block;
    -webkit-transform:translate(0,50%);
    transform:translate(0,50%);
  }
  header .right span{
    display: inline-block;
    font-size: 16px;
    text-align: right;
    font-weight: normal;
    height: 42px;
    line-height: 20px;
    color: #333;
    vertical-align: middle;}
  header .right span p{}
  header .right span p.week{}
  header .right span p.date{}
  header .right p.time{
    display: inline-block;
    font-size: 40px;
    text-align: left;
    font-weight: normal;
    height: 42px;
    line-height: 42px;
    border-left: 1px #d3d3d3 solid;
    margin-left: 10px;
    padding-left: 10px;
    color: #333;
    vertical-align: middle;
  }
  .bookList_wrap {
    position: absolute;
    left: 50%;
    top: 50%;
    transform: translate(-50%, -50%);
  }
  footer{
    width:100%;
    position:fixed;
    left:0;
    bottom:28px;
  }
  footer button{
    width:156px;
    height:48px;
    border-radius:3px;
    color:#fff;
    text-align:center;
    cursor:pointer;

    text-indent:20px;
    float:right;
    margin-right:5%;
    font-size:1.2em;
    background:url(../assets/homeIcon.png) no-repeat 25px -345px;
    background-color:#89a7e6;
  }
</style>



