<template>
  <div class="tips shadow" v-show="this.showFlog">
    <span>{{ this.tipsText }}</span>
  </div>
</template>

<script>

  export default {
    name: 'Tips',
    data() {
      return {
        tipsText: '我是提示语',
        showFlog: false
      }
    },
    methods: {
      initData(data) {
        this.tipsText = data
        this.showFlog = true
        setTimeout(() => {
          this.showFlog = false
        },1500)
      }
    }
  }
</script>

<style scoped>
  .tips.shadow{
    position:fixed;
    left:0;
    right:0;
    top:0;
    bottom:0;
    background:rgba(138,138,138,0.4);
    z-index:1000;
  }
  .tips span{
    font-size:.25rem;
    text-align:center;
    color:#fff;
    background-color:rgba(51,51,51,0.6);
    border-radius:1rem;
    position:fixed;
    top:50%;
    left:50%;
    -webkit-transform:translate(-50%,-50%);
    transform:translate(-50%,-50%);
    padding:.2rem .4rem;
    -moz-box-shadow:2px 2px 10px rgba(0, 0, 0, 0.41);
    -webkit-box-shadow:2px 2px 10px rgba(0, 0, 0, 0.41);
    box-shadow:2px 2px 10px rgba(0, 0, 0, 0.41);
  }

</style>



